import React, { forwardRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { setVisibleBanner } from 'store/actions'
import { Waypoint } from 'react-waypoint'

const withWaypoint = WrappedComponent => {
  return props => {
    const isScrolling = useSelector(store => store.ui.isScrolling)
    const visibleID = useSelector(store => store.ui.visibleID)
    const dispatch = useDispatch()
    const { id } = props

    const onLeave = evt => {
      if (!isScrolling && visibleID === id) {
        dispatch(setVisibleBanner(null))
      }
    }

    const BlockWithRef = forwardRef((propsInner, ref) => {
      return <WrappedComponent innerRef={ref} {...propsInner} {...props} />
    })

    return (
      <Waypoint
        onEnter={() => {
          if (!isScrolling) {
            dispatch(setVisibleBanner(id))
          }
        }}
        onLeave={onLeave}
        bottomOffset='40%'
        topOffset='40%'
      >
        <BlockWithRef />
      </Waypoint>
      )
  }
}

export default withWaypoint
