import React from 'react'
import css from './Meta.module.scss'
import Container from 'components/Grid/Container'
import Heading from 'components/Heading/Heading'
import CardMeta from 'components/Cards/CardMeta/CardMeta'
import IconPlay from 'assets/icons/IconPlay'
import { useDispatch } from 'react-redux'
import { toggleModal } from 'store/actions'
import ModalVideo from 'components/ModalVideo/ModalVideo'
import withWaypoint from 'hoc/withWaypoint'

const Meta = ({ id, heading, descriptor, cards, innerRef }) => {
  const dispatch = useDispatch()

  const renderCards = list => list.map((card, index) => <CardMeta {...card} key={`Meta card#${index}`} />)

  const handleClickVideo = () => {
    dispatch(toggleModal(true, <ModalVideo />, {
      isCloseBtnRequired: false
    }))
  }

  return (
    <section
      id={id}
      ref={innerRef}
    >
      <div className={css.frame}>
        <Container className={css.wrapper}>
          <Heading className={css.heading} label={heading} />
          <p className={css.descriptor} dangerouslySetInnerHTML={{ __html: descriptor }} />
          <ul className={css.list}>
            { renderCards(cards) }
          </ul>
          <button
            className={css.play}
            onClick={handleClickVideo}
            type='button'
          >
            <IconPlay className={css.icon} />
            Переглянути відео
          </button>
        </Container>
      </div>
    </section>
  )
}

export default withWaypoint(Meta)
