import React from 'react'
import css from './Portfolio.module.scss'
import Container from 'components/Grid/Container'
import Heading, { HeadingTypes } from 'components/Heading/Heading'
import Button from 'components/Button/Button'
import { ButtonSizes } from 'utils/const'
import CalculatorForm from 'Pages/Home/Portfolio/CalculatorForm/CalculatorForm'
import withWaypoint from 'hoc/withWaypoint'

const Portfolio = ({ id, heading, descriptor, subheading, form, resultLabel, buttonLabel, innerRef }) => {
  return (
    <section
      id={id}
      ref={innerRef}
    >
      <Container className={css.wrapper}>
        <Heading
          className={css.heading}
          label={heading}
        />
        <p className={css.descriptor}>
          { descriptor }
        </p>
        <Heading
          className={css.subheading}
          label={subheading}
          type={HeadingTypes.SUBHEADING}
        />
        <CalculatorForm
          className={css.form}
          items={form}
          resultLabel={resultLabel}
        />
        <Button
          className={css.button}
          label={buttonLabel}
          handleClickButton={() => {}}
          size={ButtonSizes.MEDIUM}
        />
      </Container>
    </section>
  )
}

export default withWaypoint(Portfolio)
