import React from 'react'
import css from './Copyright.module.scss'
import classnames from 'classnames'
import { useDispatch } from 'react-redux'
import { toggleModal } from 'store/actions'
import ModalDisclaimer from 'components/ModalDisclaimer/ModalDisclaimer'

const Copyright = ({ className }) => {
  const dispatch = useDispatch()
  const currentYear = new Date().getFullYear()

  const handleClickButton = () => {
    dispatch(toggleModal(true, (
      <ModalDisclaimer />
    )))
  }

  return (
    <div className={css.wrapper}>
      <p className={classnames(css.copyright, className)}>
        Ваш інвестиційний консультант<br/><a href='/#' className={css.link}>HUG'S UKRAINE</a>  © { currentYear }
      </p>
      <button
        className={css.button}
        onClick={handleClickButton}
        type='button'
      >
        Open modal with additional info about HUGS
      </button>
    </div>
  )
}

export default Copyright
