import React from 'react'
import css from './ModalTableAdaptive.module.scss'
import classnames from 'classnames'
import IconOK from 'assets/icons/IconOK'
import Container from 'components/Grid/Container'
import { Link } from 'react-router-dom'

const ModalTableAdaptive = ({ className, headings, rows, results }) => {
  const renderHeadings = row => {
    const headingItems = headings.map(({ labelAdaptive, bgColor }, index) => (
      <li
        className={css.item}
        key={`Modal table heading#${index}`}
      >
        <p
          className={css.label}
          dangerouslySetInnerHTML={{ __html: labelAdaptive }}
        />
        {(row[index + 1]?.status || row[index + 1].label) &&
          <IconOK className={css.iconOK} />
        }
      </li>
    ))

    return (
      <ul
        className={css.list}
        style={{
          display: 'grid',
          gridTemplateColumns: `repeat(${headings.length}, 1fr)`,
          gridAutoRows: '8rem'
        }}
      >
        { headingItems }
      </ul>
    )
  }

  const renderRows = rows => rows.map((row, index) => {
    const { key, value } = row[0]

    return (
      <li className={css.group} key={index}>
        <Container>
          { key && <h3 className={css.key} dangerouslySetInnerHTML={{__html: key}} /> }
          { value && <p className={css.value} dangerouslySetInnerHTML={{__html: value}} /> }
        </Container>
        { renderHeadings(row) }
      </li>
    )
  })

  const renderResults = () => {
    const cells = results.slice(1).map(({ link }, index) => (
      <li
        className={css.result}
        style={{ backgroundColor: link.bgColor }}
        key={index}
      >
        <Link
          to={link.url}
          className={css.link}
        >
          <p
            className={css.label}
            dangerouslySetInnerHTML={{ __html: headings[index].label }}
          />
          {link.label &&
            <span
              className={css.price}
              dangerouslySetInnerHTML={{ __html: link.label }}
            />
          }
          {link.notification &&
            <span className={css.notification}>
              {link.notification}
            </span>
          }
        </Link>
      </li>
    ))

    return (
      <>
        <p className={css.resultsLabel} dangerouslySetInnerHTML={{ __html: results[0].label }} />
        <ul
          className={css.results}
          style={{
            display: 'grid',
            gridTemplateColumns: `repeat(${headings.length}, 1fr)`,
            gridAutoRows: '14rem'
          }}
        >
          { cells }
        </ul>
      </>
    )
  }

  return (
    <div className={classnames(css.wrapper, className)}>
      <ul
        className={css.groups}
      >
        { renderRows(rows) }
      </ul>
      { renderResults() }
    </div>
  )
}

export default ModalTableAdaptive
