import React, { useState } from 'react'
import css from './ModalDetails.module.scss'
import classnames from 'classnames'
import { data } from 'utils/data'
import { useDispatch, useSelector } from 'react-redux'
import { toggleModal } from 'store/actions'
import { DeviceTypes, ModalThemes } from 'utils/const'
import IconClose from 'assets/icons/IconClose'
import ModalTable from 'components/ModalDetails/ModalTable/ModalTable'
import IconCloseAdaptive from 'assets/icons/IconCloseAdaprive'
import IconLogo from 'assets/icons/IconLogo'
import ModalTableAdaptive from 'components/ModalDetails/ModalTable/ModalTableAdaptive'

const ModalDetails = ({ defaultTab = 0 }) => {
  const { heading, tabs } = data.modalDetails
  const deviceType = useSelector(state => state.elastic.deviceType)
  const [activeTab, setActiveTab] = useState({
    index: defaultTab,
    theme: tabs[defaultTab].theme
  })
  const dispatch = useDispatch()

  const renderSwitches = list => list.map(({ label, theme }, index) => (
    <li className={css.item} key={`Tab switch#${index}`}>
      <button
        className={classnames(css.trigger, {
          [css.triggerActive]: activeTab.index === index
        })}
        onClick={() => setActiveTab({
          index,
          theme
        })}
        type='button'
      >
        { label }
      </button>
    </li>
  ))

  const handleCloseModal = () => {
    dispatch(toggleModal(false, null))
  }

  return (
    <div className={classnames(css.wrapper, {
      [css.wrapperBlack]: activeTab.theme === ModalThemes.BLACK,
      [css.wrapperPurple]: activeTab.theme === ModalThemes.PURPLE,
    })}>
      {deviceType === DeviceTypes.DESKTOP &&
        <button
          className={css.buttonClose}
          onClick={handleCloseModal}
          type='button'
        >
          <IconClose className={css.iconClose} />
          Close modal
        </button>
      }
      {deviceType === DeviceTypes.ADAPTIVE &&
        <header className={css.header}>
          <IconLogo className={css.logo} />
          <button
            className={css.buttonClose}
            onClick={handleCloseModal}
            type='button'
          >
            <IconCloseAdaptive className={css.iconClose} />
            Close modal
          </button>
        </header>
      }
      <div className={css.intro}>
        <h2
          className={css.heading}
          dangerouslySetInnerHTML={{ __html: heading }}
        />
        <ul className={css.list}>
          { renderSwitches(tabs) }
        </ul>
      </div>
      {deviceType === DeviceTypes.DESKTOP &&
        <ModalTable
          className={css.table}
          {...tabs[activeTab.index].table}
        />
      }
      {deviceType === DeviceTypes.ADAPTIVE &&
        <ModalTableAdaptive
          className={css.table}
          {...tabs[activeTab.index].table}
        />
      }
    </div>
  )
}

export default ModalDetails
